.about-container {
	height: 100vh;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: left;
}

#content {
	display: flex;
	flex-direction: column;
	justify-content: left;
	padding: 100px;
}



#content #upper {
	margin-bottom: 15vh;
	font-size: 1.2vw;
}

#upper h1 {
	font-size: 4vw;
}

#content #lower {
	margin-bottom: 15vh;
	font-size: 1.2vw;
}
