#content button {
  display: inline-block; 
  position: relative;
  font-size: 1.35vw;
  cursor: pointer;
  background: none;
  border: none;
  color: white;
  width: 9vw;
}

.underline-animation::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: white;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

