:root {
/*   --background: #253842; */
  --background: linear-gradient(206.15deg, #06182E 12.92%, #000000 83.54%);


  --green: #219653;
  --blue: #2F80ED;
  --purple: #9B51E0;
  --red: #EB5757;
  --orange: #F2994A;
  --mauve: #BB6BD9;
  --yellow: #F2C94C;
}

@import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

html, body {
  background: var(--background);
/*   background: -webkit-gradient(linear, 100% 0%, 100% 0%, from(black), to(white)); */
/*   background: -webkit-gradient(linear, 0% 0% 0% 100%, from(rgba(6, 24, 46)), to(rgba(0,0,0,0))); */
  color: white;
  overflow: hidden;
  min-height: 100vh;
  min-width: 100vw;
  font-family: 'Inter', sans-serif;

/*   animation: bg-fade 5s; */
/*   animation-delay: 7s; */
}

h1, h2, h3, h4 {
  font-family: 'Gloock', serif;
  color: white;
  margin-top: 0;
}

@keyframes bg-fade {
  from {
    background: black;
  }
  to {
    background: var(--background);
  }
}

#tag {
  font-size: 5vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Individual Blocks */

.block {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  border-radius: 1vmax;
  height: 15vh;
  width: 18vw;

  padding: 1%;

  background: var(--red);
  color: transparent;
  cursor: pointer;

  transition: transform 500ms ease;
  transition: 500ms ease-in;
  opacity: 0;
  animation: 1000s ease-in appear;

}

@keyframes appear {
  0% {
    opacity: 0;

  }
  0.1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.block:hover {
  color: white;
  transform: scale(1.1);
}

.block:nth-child(1) {
  background: var(--green);
  top: 10vh;
  left: 20vw;
  animation-delay: 0.5s;
}

.block:nth-child(2) {
  background: var(--blue);
  top: 15vh;
  left: 60vw;
  animation-delay: 1s;
}


.block:nth-child(3) {
  background: var(--purple);
  top: 45vh;
  left: 5vw;
  animation-delay: 1.5s;
}

.block:nth-child(4) {
  background: var(--red);
  top: 75vh;
  left: 35vw;
  animation-delay: 2s;
}

.block:nth-child(5) {
  background: var(--orange);
  top: 55vh;
  left: 75vw;
  animation-delay: 2.5s;
}


#title {
  font-size: 1.5vw;
  font-weight: 600;
}

#summary {
  font-size: 1vw;
  font-weight: 300;
  margin-top: -1.5vh;
}

/* Navigation */
nav {
  max-width: 15vw;

  display: flex;
  justify-content: space-around;
  align-items: center;

  margin-top: 94vh;
  margin-left: 80vw;
  color: white;
  bottom: 10vw;

  opacity: 0;
  animation: 1000s ease-in appear;
  animation-delay: 1.5s;

  
}

nav span {
  display: inline-block; 
  position: relative;
  font-size: 1.5vw;
  cursor: pointer;
}

.underline-animation::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: white;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}


/* Stopping Mobile Viewers */
@media (max-width:801px) {
  body {
    background: red; 
  }
}